nav {
    background: rgba(100, 97, 97, 0.5);
    width: max-content;
    display: block;
    padding: 1rem 1.5rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 2rem;
    border-radius: 2rem;
    backdrop-filter: blur(1rem);
}

nav a {
    background: transparent;
    padding: 0.5rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
}

nav a:hover {
    background: rgba(0, 0, 0, 0.2);
}

nav a.active {
    background: var(--color-bg);
    color: var(--color-white);
}