header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header_container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* ============ CTA =========== */

.cta {
    margin-top: 2.5rem;
    display: flex;
    justify-content: center;
    gap: 1.5rem;
}

/* ============ Socials =========== */
.header_socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    left: 0;
    bottom: 3rem;
    position: absolute;
}

.header_socials::after {
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ============ image =========== */
.profile_pic {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.profile_pic {
    background: linear-gradient(var(--color-primary), transparent);
    width: 23rem;
    height: 23rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 4rem;
    border-radius: 50%;
    overflow: hidden;
    padding: 3rem 1.5rem 1.5rem 1.5rem;
}

/* ============ image =========== */
.scroll_down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1rem;
}

/*  =========== MEDIA QUERIES (MEDIUM DEVICES) =========== */
@media screen and (max-width: 1024px) {
    header {
        height: 68hv;
    }
}

/*  =========== MEDIA QUERIES (SMALL DEVICES) =========== */
@media screen and (max-width: 600px) {
    header {
        height: 100hv;
    }

    .header_socials, .scroll_down {
        display: none;
    }
}